import { Component, OnInit } from '@angular/core';
import {IPizza} from '../pizza.types';
import {AngularFireAuth} from '@angular/fire/auth';
import {AngularFirestore, AngularFirestoreCollection} from '@angular/fire/firestore';
import {Observable} from 'rxjs';
import {FormArray, FormControl, FormGroup} from '@angular/forms';

@Component({
  selector: 'app-new',
  templateUrl: './new.page.html',
  styleUrls: ['./new.page.scss'],
})
export class NewPage implements OnInit {

  form: FormGroup;
  private itemsCollection: AngularFirestoreCollection<IPizza>;
  pizzas: Observable<IPizza[]>;

  newPizza: IPizza = {
      name: '',
      description: '',
      alergens: []
  };

  constructor(private afs: AngularFirestore, public afAuth: AngularFireAuth) {}

  ngOnInit() {
      this.initForm();
      this.itemsCollection = this.afs.collection<IPizza>('allpizzas');
      this.pizzas = this.itemsCollection.valueChanges();
  }
  onAddPizza() {
      console.log('Add pizza', this.newPizza);
      console.log(this.pizzas);

      this.itemsCollection.add(this.newPizza).then((done) => {
          if (done) { console.log('Successfully added'); }
      });
  }
  initForm() {
    this.form = new FormGroup({
        name: new FormControl(''),
        description: new FormControl(''),
        alergens: new FormArray([]),
    });
  }
}
